.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  padding: 10px;
  margin: 10px auto;
  border-radius: 4px;
  background-color: #cef3688e;
}

.title::first-letter {
  text-transform: uppercase;
  line-height: 0;
}
